@import url(http://fonts.googleapis.com/css?family=Roboto+Slab|Open+Sans:400italic,700italic,400,700);
body{margin:0;padding:0;background:#1F1B24;color:white;font-family:"Roboto Slab",serif}#root{margin:0 24px;position:relative;min-height:100vh}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

.todo-counter{font-size:24px;text-align:center;margin:0;padding:48px}

.todo-search{background:#343136;border-radius:2px;border:2px solid #202329;margin:0 18px;border-radius:20px;height:64px;width:calc(100% - 64px);font-size:24px;font-family:'Montserrat';font-weight:400;color:#1E1E1F;box-shadow:0px 5px 50px rgba(32,35,41,0.25);padding-left:20px;transition:275ms ease;-webkit-transition:275ms ease}.todo-search:-ms-input-placeholder{color:whitesmoke;font-family:'Montserrat';font-weight:400}.todo-search::placeholder{color:whitesmoke;font-family:'Montserrat';font-weight:400}.todo-search:focus{color:white;outline-color:white}

ul{margin:0;padding:30px 0 56px 0;list-style:none}

.todo-item{background-color:#1F1B24;position:relative;display:flex;justify-content:center;align-items:center;margin-top:24px;box-shadow:0px 5px 50px rgba(32,35,41,0.15);border-width:1px;border-style:solid;border-color:white;border-radius:10px}.todo-item .todo-item-p{margin:24px 0 24px 44px;width:calc(100% - 120px);font-size:18px;line-height:24px;font-weight:400}.todo-item .todo-item-p--complete{opacity:0.4;text-decoration:line-through}.todo-item .icon{cursor:pointer;display:flex;justify-content:center;align-items:center;height:48px;width:48px;font-size:24px;font-weight:bold}.todo-item .icon-check{position:absolute;top:6px;left:12px}.todo-item .check-img{width:3.55rem;height:3.55rem;transition:275ms ease;-webkit-transition:275ms ease;-webkit-mask:url(/to-do-platzi/static/media/check.17d2d72e.svg) no-repeat center;mask:url(/to-do-platzi/static/media/check.17d2d72e.svg) no-repeat center;background-color:white;cursor:pointer;margin:auto}.todo-item .check-img:hover{background-color:green}.todo-item .icon-check--active>.check-img{background-color:green}.todo-item .delete-img{width:3.1rem;height:3.1rem;transition:275ms ease;-webkit-transition:275ms ease;-webkit-mask:url(/to-do-platzi/static/media/delete.2a6dcf09.svg) no-repeat center;mask:url(/to-do-platzi/static/media/delete.2a6dcf09.svg) no-repeat center;background-color:white;cursor:pointer;margin:auto}.todo-item .delete-img:hover{background-color:red}

.create-todo-button{z-index:1;background-color:green;box-shadow:0px 0px 10px rgba(97,250,135,0.5);border:none;border-radius:50%;cursor:pointer;font-size:50px;position:fixed;bottom:24px;right:24px;font-weight:bold;color:#FAFAFA;display:flex;justify-content:center;align-items:center;height:64px;width:64px;transform:rotate(0);transition:.3s ease}.create-todo-button.open-true{transform:rotate(224deg);background-color:#b40202;box-shadow:0px 0px 10px rgba(226,112,112,0.5)}.create-todo-button.open-false{transform:rotate(0)}

.modal-background{background:rgba(32,35,41,0.8);position:fixed;top:-10px;left:-10px;right:-10px;bottom:-10px;display:flex;justify-content:center;align-items:center;color:white}

form{width:90%;max-width:300px;background-color:#1F1B24;padding:33px 40px;display:flex;align-content:center;flex-direction:column;border-width:1px;border-color:white;border-style:solid;border-radius:16px}form label{text-align:center;font-weight:bold;font-size:#1E1E1F;margin-bottom:26px}form textarea{background:#343136;border:1px, solid white;border-radius:2px;box-shadow:0px 5px 50px rgba(32,35,41,0.25);color:white;font-size:20px;text-align:center;padding:12px;height:96px;width:calc(100%-25px);resize:none}form textarea:-ms-input-placeholder{color:#a5a5a5;font-family:'Montserrat';font-weight:400}form textarea::placeholder{color:#a5a5a5;font-family:'Montserrat';font-weight:400}form .todo-form-button-container{margin-top:14px;display:flex;justify-content:space-between;align-items:center;width:100%}form .todo-form-button-container .todo-form-button{cursor:pointer;display:inline-block;font-size:20px;color:#202329;font-weight:400;width:120px;height:48px;border-radius:2px;border:none}form .todo-form-button-container .todo-form-button-add{background-color:green;box-shadow:0px 0px 10px rgba(97,250,135,0.5);color:white;font-family:"Roboto Slab",serif;margin:auto}form .todo-form-button-container .todo-form-button-cancel{background:transparent}

.loading-todo-container{position:relative;display:flex;justify-content:center;align-items:center;margin-top:24px;height:72px;box-shadow:0px 5px 50px rgba(32,35,41,0.15);border-width:1px;border-style:solid;border-color:white;border-radius:10px}.loading-todo-container .loading-todo-text{margin:24px 0 24px 24px;width:calc(100% - 120px);font-size:18px;line-height:24px;font-weight:400}.loading-todo-container,.loading-todo-complete-icon,.loading-todo-delete-icon{background:linear-gradient(90deg, #555, #252525);background-size:400% 400%;animation:loadingAnimation 3s ease-in-out infinite}@keyframes loadingAnimation{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}

